.app-wrapper{
  margin: 0 auto;
  display: grid;
  grid-template-areas:
  "h h"
  "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  width: 1200px;
  /*grid-gap: 10px;*/
}
.header{
  grid-area: h;
  background-color: darkgreen;
}
.header img {
  width: 20px;
}
.nav{
  grid-area: n;
  background-color: burlywood;
}
.content{
  grid-area: c;
  background-color: cadetblue;
}